@-webkit-keyframes move {
    0% {left: 5%;}
    50% {left: calc(95% - 82px)}
    100% {left: 5%}
}

@keyframes move {
    0% {left: 5%;}
    50% {left: calc(95% - 82px)}
    100% {left: 5%}
}

@-webkit-keyframes flash {
    0% {-webkit-filter: brightness(100%);filter: brightness(100%);}
    50% {-webkit-filter: brightness(200%);filter: brightness(200%);}
    100% {-webkit-filter: brightness(100%);filter: brightness(100%);}
}

@keyframes flash {
    0% {-webkit-filter: brightness(100%);filter: brightness(100%);}
    50% {-webkit-filter: brightness(200%);filter: brightness(200%);}
    100% {-webkit-filter: brightness(100%);filter: brightness(100%);}
}

#clock {
	-webkit-animation-name: move;
	        animation-name: move;
	-webkit-animation-duration: 10s;
	        animation-duration: 10s;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-timing-function: cubic-bezier(0.4, 0.14, 0.6, 0.89);
	        animation-timing-function: cubic-bezier(0.4, 0.14, 0.6, 0.89);
}

.single-unit {
	-webkit-animation-name: flash;
	        animation-name: flash;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-timing-function: cubic-bezier(0.4, 0.14, 0.6, 0.89);
	        animation-timing-function: cubic-bezier(0.4, 0.14, 0.6, 0.89);
}

.swatches-picker {
	width: 100%!important;
	height: 100%!important;
}


